@charset "UTF-8";
body {
  font-family: "ヒラギノ角ゴ Pro W3","Hiragino Kaku Gothic Pro","メイリオ",Meiryo,Osaka,"ＭＳ Ｐゴシック","MS PGothic",sans-serif;
}

body,
input,
select {
  color: #3c3c3c;
}

a {
  color: #0033cc;
}

a:visited {
  color: rebeccapurple;
}

.left {
  float: left;
}

.right {
  float: right;
}

.mcf:after {
  content: ".";
  display: block;
  height: 0;
  font-size: 0;
  clear: both;
  visibility: hidden;
}

.txt {
  overflow: hidden;
  background-repeat: no-repeat;
  white-space: nowrap;
  text-indent: 100%;
}
